<script>
import {onMount,tick,onDestroy} from 'svelte'
import Player from './lib/Player'


onMount(()=>{ 

	const body = document.querySelector('body');
	body.addEventListener('awesome', handleOnPlay);
		
	return ()=>{
	body.removeEventListener('awesome', handleOnPlay);
	}
	
})


 let status = 1 // 0加载中  1加载结束或未开始加载
 let events = []
 let eventsURL = "https://qiniu.testbay.cn/snapshot/new/abc.json"
 let messageObj ={
	 open:false,
	 status:'negative', //negative, success
	 memo:''
 } // 消息开关

 let sessionIndex = 0
 let detail = {} 
 let players = []
 let playerInstance = null
 let currentEvents=[]

let need_multiple_player = false




 const handleOnPlay = (e)=>{
	 if(e.detail){
		detail=e.detail
	 }
 }
 
 const initPlayer = (currentEvents)=>{
	
	 if(playerInstance){ 
		 playerInstance.destroy()
		 playerInstance=null
		 detail={}
	 }

	 playerInstance = new Player({
				target: document.querySelector("#player"),
				data: {
					events:currentEvents,
					autoPlay: false,
    				skipInactive: false,
				}
			})

// clearNode(document.querySelector(`#player`))
	
 }
 const handleClick = async ()=>{ 
	 // pending
	 status = 0
	 sessionIndex=0
	 try{
		 if(eventsURL==''){
			 throw new Error('events url can not be empty')
		 }
		// 拉数据
		const response = await fetch(eventsURL,{
			timeout: 1000 * 60 * 2,
		})
		const json = await response.json()

		if(Object.prototype.toString.call(json[0]) === '[object Array]'){
			need_multiple_player=true
		}
		
		if(!need_multiple_player && json.length<2){
			throw new Error('events length must beyond 2')
		}
		messageObj={...messageObj,open:true,status:'success'}
		await tick()
		status=1
		await tick()
		//  渲染播放器
		events=json 
		await tick()

		if(need_multiple_player){
			initPlayer(events[0])
		}else{
			initPlayer(events)
		}
	 }catch(err){
		messageObj={...messageObj,open:true,status:'negative',memo:err}
		await tick()
		status=1
	 }
	 
 }

 function clearNode(node){ 
    while(node.hasChildNodes())  {
        node.removeChild(node.firstChild);
    }
}

const close = ()=>{
		 messageObj={...messageObj,open:false}
}
 
     
</script>


<div class="ui two column grid" style="max-width:1050px">

  <div class="row">
    <div class="ten wide column">	 
	 <h1 class="ui header">Web录像播放器</h1>
	</div>

</div>
  <div class="row">
   <div class="sixteen wide column">	
		<div class="ui {status===0?'disabled':''} fluid action input">
			<input bind:value={eventsURL} type="text" placeholder="输入录像地址">
			<button on:click={handleClick} class="ui {status===0?'loading disabled':''} button">加载</button>
			
		</div>

<!-- 操作反馈 -->
{#if messageObj.open}
		{#if   messageObj.status=="success"}
			<div class="ui success message">
			<i on:click={close} class="close icon"></i>
			<div class="header">录像加载完成 </div>
			</div>
		{/if}

		{#if  messageObj.status=="negative"}
		<div class="ui negative message">
		<i on:click={close} class="close icon"></i>
		<div class="header">录像加载失败</div>
		<p>{messageObj.memo} </p></div>
		{/if}
{/if}
	</div>
 
  </div>

{#if need_multiple_player}
	<div class="row">
	   <div class="column">
		{#each events as currentEvents, i}

		<button class="ui primary {i===sessionIndex?'':'basic'}  button" on:click={()=>{
			sessionIndex=i
			initPlayer(currentEvents)
		}}>session {i+1}</button>
  
 	{/each}
	 </div>
  </div>

{/if}


  <div class="row">
    <div class="eight wide column">
	<div id='player'></div>

	</div>
  </div>


  <div class="row">
    <div class="sixteen wide column">


<div class="ui message">
 
			{JSON.stringify(detail)}
	 
</div>
		

	</div>
  </div>
 
 
  
</div>

 



<style>

 #json {
	   width: 100%;
  min-height: 30rem;
  font-family: "Lucida Console", Monaco, monospace;
  font-size: 0.8rem;
  line-height: 1.2;
 }
</style>